*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

html {
	font-size: 62.5%; 
}

body {
	box-sizing: border-box;
	font-size: 1.6rem;
	font-family: Arial, Helvetica, sans-serif;
	font-weight: 700;
	line-height: 2.25;
	color: lightblue;
}

#root {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}


